@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;200;300;400;500;700;900&display=swap);
*{box-sizing:border-box}body{margin:0;font-family:Roboto,sans-serif;color:#121212}.App{margin:16px;max-width:100vw}.App__heading{font-weight:500;font-size:24px;line-height:28px;text-transform:uppercase;padding:14px 14px 20px}.App__main{display:flex;left:0;right:0}
.facet{width:366px}@media(max-width: 767px){.facet{max-width:366px}}.facet__head{position:relative;display:flex;justify-content:space-between;align-items:center;height:54px;cursor:pointer}.facet__head::before{position:absolute;content:"";left:0;right:0;height:1px;background-color:#efefef;transform:translateY(-27px)}.facet__head:last-child::after{position:absolute;content:"";left:0;right:0;height:1px;background-color:#efefef;transform:translateY(27px)}.facet__name{padding-left:22px}.facet__options{display:flex;flex-direction:column}.facet__option{display:flex;justify-content:space-between;align-items:center;font-size:14px;line-height:16px;padding:8px 18px 8px 22px}.facet__option-wrapper{display:flex;flex-direction:row;align-items:center;cursor:pointer}.facet__box{position:relative;background-size:cover;width:20px;height:20px;transition:.3s;background-color:transparent;border:1px solid #ccc;border-radius:1px}.facet__box:hover{border:1px solid #121212}.facet__box--selected::before{position:absolute;content:"";left:3px;right:4px;bottom:5px;background-repeat:no-repeat;width:12px;height:8px;background-image:url(../../static/media/checkmark.f79b3f3b.svg)}.facet__value{padding-left:8px}.facet__count{color:#939393}.facet__button{width:54px;height:54px;background-repeat:no-repeat;background-position:center;cursor:pointer;background-color:transparent;border:none}.facet__button-open{background-image:url(../../static/media/Plus.658cc4c2.svg)}.facet__button-close{background-image:url(../../static/media/Minus.7e4c869a.svg)}.facet__color{position:relative;width:20px;height:20px;border:1px solid transparent;background-color:transparent;border-radius:50%;transition:.3s;cursor:pointer}.facet__color:hover{transform:scale(110%)}.facet__color--selected-color::before{position:absolute;content:"";left:4px;right:4px;bottom:4px;background-repeat:no-repeat;width:12px;height:8px;background-image:url(../../static/media/checkmark.f79b3f3b.svg)}.facet__more-button{display:flex;justify-content:center;align-items:flex-start;font-weight:600;font-size:14px;line-height:16px;background-color:transparent;border:1px solid transparent;margin:2px 0 28px;cursor:pointer}.facet__more-button-word{line-height:16px}.facet__more-button-word::before{position:absolute;content:"";height:.5px;width:10px;background-color:#121212;transform:translate(-18px, 7px)}.facet__more-button-word--plus::after{position:absolute;content:"";height:.5px;width:10px;background-color:#121212;transform:translate(-50.5px, 7px) rotate(90deg)}.facet__go-button{background-color:transparent;border:1px solid transparent;font-weight:500;font-size:14px;text-transform:uppercase;color:#121212;cursor:pointer}.facet:last-child .facet__wrapper .facet__options .facet__more-button{display:none}
.ProductsList{color:#000;width:100%}.ProductsList-container{max-width:1070px;margin:0 auto}.ProductsList-wrapper{display:flex;flex-wrap:wrap;justify-content:center;padding-left:24px;grid-gap:18px;gap:18px}.ProductsList-card{display:flex;flex-direction:column;align-items:center;margin-bottom:15px}.ProductsList-card__image{width:247px;height:346px;object-fit:contain;background-color:#f7f7f7}.ProductsList-card-wrapper{position:relative}.ProductsList-card__image-sale:before{position:absolute;display:flex;justify-content:center;align-items:center;font-size:14px;content:"SALE";color:#fff;width:82px;height:30px;background-color:#82b8d9;top:0;right:0}.ProductsList-card__image-discount{position:absolute;display:flex;top:0}.ProductsList-card__image-discount-number{width:80px;height:30px;font-size:14px;line-height:30px;text-align:center;color:#fff;background-color:#82b8d9;top:0;left:0}.ProductsList-card__image-discount-after{height:0;width:0;border-top:15px solid transparent;border-left:15px solid #82b8d9;border-bottom:15px solid transparent;border-right:15px solid transparent}.ProductsList-card__title{font-size:12px;font-weight:500;color:#000;text-decoration:none;text-transform:uppercase;margin:18px 0}.ProductsList-card__price-info{display:flex;grid-gap:10px;gap:10px;font-weight:bold;font-size:14px;line-height:15px}.ProductsList-card__old-price{color:#939393}
.Filters{display:grid;grid-template-columns:366px 1fr}.Filters__section{font-weight:bold;font-size:14px;line-height:16px;text-transform:uppercase;margin:30px 0 22px 14px}.Filters__section::before{position:absolute;content:"";height:1px;background-color:#efefef;transform:translateY(-27px);left:14px;right:14px}
.Breadcrumbs{display:flex;flex-direction:row;flex-wrap:wrap;align-items:center;color:#939393;grid-gap:12px;gap:12px;padding:14px 24px}.Breadcrumbs__piece{display:flex;flex-direction:row;align-items:center;font-size:14px;line-height:16px}.Breadcrumbs__facet{position:relative;display:flex;flex-direction:row;align-items:center;cursor:pointer}.Breadcrumbs__facet:hover::before{position:absolute;content:"";width:100%;height:1px;background-color:#939393;transform:translateX(-2px)}.Breadcrumbs__button{width:11px;height:11px;background-image:url(../../static/media/cross.d169095a.svg);background-repeat:no-repeat;background-size:contain;padding:0 10px;background-position:center;border:1px solid transparent;background-color:transparent}.Breadcrumbs__color{width:16px;height:16px;border-radius:50%}.Breadcrumbs__slash{padding-left:10px}.Breadcrumbs__piece-wrapper:last-child .Breadcrumbs__piece .Breadcrumbs__slash{display:none}
